import {getInput} from "../../components/input";
import {Templator} from "../../utils/Templator";
import {getButton} from "../../components/button";

export function templateAuth() {
    let templator = new Templator();
    return "<form class='block-auth' method='post' action=''>" +
        "<h1>–í—Ö–æ–¥</h1>" +
        templator.template(getInput()).compile({inputId: "input_login", inputLabel: "–õ–æ–≥–∏–Ω", inputName: "login", inputType: "text"}) +
        templator.template(getInput()).compile({inputId: "input_password", inputLabel: "–ü–∞—Ä–æ–ª—å", inputName: "password", inputType: "password"}) +
        templator.template(getButton()).compile({buttonType: "submit", buttonClass: "btn-primary", buttonId: "button-auth", buttonText: "–í–æ–π—Ç–∏"}) +
        "<a href='registartion'>–ù–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞?</a> " +
        "</form>";
}