import {Templator} from "../../../../utils/Templator";
import {getButton} from "../../../../components/button";

export function templateProfileButtons() {
    let templator = new Templator();
    return "<article class='block-buttons-profile'>" +
        templator.template(getButton()).compile({buttonType: "button", buttonClass: "btn-primary", buttonId: "button-edit", buttonText: "–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å" }) +
        templator.template(getButton()).compile({buttonType: "button", buttonClass: "btn-primary", buttonId: "button-pass", buttonText: "–ò–∑–º–µ–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å" }) +
        templator.template(getButton()).compile({buttonType: "button", buttonClass: "btn-danger", buttonId: "button-exit", buttonText: "–í—ã—Ö–æ–¥" }) +
        "</article>";
}