import {Templator} from "../../../../utils/Templator";
import {getButton} from "../../../../components/button";
import {getTextarea} from "../../../../components/textarea";

export function templateEnter() {
    let templator = new Templator();
    return "<section class='block-enter'>" +
        templator.template(getButton()).compile({buttonClass: "btn-primary", buttonId: "button-send", buttonText: "–û—Ç–ø—Ä–∞–≤–∏—Ç—å", buttonType: "button"}) +
        templator.template(getTextarea()).compile({textareaPlaceholder: "–í–≤–µ–¥–∏—Ç–µ —Å–æ–æ–±—â–µ–Ω–∏–µ", textareaId: "send_message", textareaName: "send_message"}) +
        "</section>";
}